import React from 'react'

function SitaRamaStotram() {
    const sitaRamaStotram = [
        {
            kannada: `  |
              ||  ||
            
                 |
              ||  ||
            
                |
              ||  ||
            
               |
              ||  ||
            
              |
              ||  ||
            
              |
              ||  ||
            
              |
               ||  ||

              |
               ||  ||
            
              |
                 ||  ||
            
                    |
                  ||  ||
            
                |
                  |
                ||  ||`

        },
        {
            english: `ayodhyapuranetaram mithilapuranayikam
            raghavanalankaram vaidehanamalankriyam || 1 ||
            
            raghunam kuladipam ca niminam kuladipikam
            suryavamsasambhutam somavamsasamudbhavam || 2 ||
            
            putram dasharathasyadyam putrim janakabhupateh
            vasishtanumatacaram shatanandamatanugam || 3 ||
            
            kausalyagarbhasambhutam vedigarbhoditam svayam
            pundarikavisalaksham sphuradindivar ekshanam || 4 ||
            
            candrakantananabhojam candrabimbopamananam
            mattamatangagamanam mattahamsavadhugatam || 5 ||
            
            candanardrabhujamadyam kunkumardrakucasthalim
            capalankritahastabjam padmalankritapanika || 6 ||
            
            sharanagatagoptaram pranipadaprasadikam
            kalamghanibham ramam kartasvarasamaprabham || 7 ||
            
            divyasimhasanasinam divyasragvastrabhushanam
            anuksanam kataksabhyam anyonyekshanakanksinau || 8 ||
            
            anyonyasadrshakarau trailokyagradampati
            imau yuvam pranamyaham bhajamyadya kritarthatam || 9 ||
            
            anena stauti yah stutyam ramam sitam ca bhaktitah
            tasya tau tanutam punyah sampadah sakalarthadah || 10 ||
            
            evam sri ramacandrasya janakyasca visesatah
            kritam hanumata punyam stotram sadyo vimuktidam
            yah patet pratarutthaya sarvan kamanavapnuyat || 11 ||`
        }
    ]
    return(
        <div className="mantra">
            <div className="mantraAudio">
                <audio src= '/Songs/shriram.mp3' controls/>
            </div>
            <div className="mantraDisplay">
                <div className="kannada">
                    {sitaRamaStotram.map((mantra)=>{
                        return(
                            <p>{mantra.kannada}</p>
                        )
                    })}
                </div>
                <div className="english">
                    {sitaRamaStotram.map((mantra)=>{
                        return(
                            <p>{mantra.english}</p>
                        )
                    })}
                </div>
            </div>
            
        </div>
    )
}

export default SitaRamaStotram
