import React from 'react'

function GayatriRamayanam() {
    const gayatriRamayanam = [
        {
            kannada: `     
                
            
                  
                  
            
                
                   
            
                  
                 
            
                 
                 
            
                    
                    
            
                   
                
            
                   
                  
            
                 
                 
            
                  
                   
            
                
                  
            
                 
                 

                   
                
            
                  
                   
            
                
                 
            
                
              
             
              
            
                 
                 
            
              
                 
              
                  
            
                 
                  
            
                 
                 
            
                
                
            
                
                  
            
                 
                 
            
                 
                  
            
                
                  `

        },
        {
            english: `
            Tapah svadhyaya-niratam tapasvi vag-vidam varam
            Naradam paripapraccha Valmikir munipungavam || 1
            
            Sa hatva rakshasan sarvan yajnaghnan Raghunandana
            Rishibhih pujitah samyak yathe'ndro vijayi pura || 2
            
            Vishvamitrah sa dharmaatma shrutva Janaka-bhashitam
            Vatsa Rama dhanuh pashya iti Raghavamabravit || 3
            
            Tustava asya tada vamsham pravisya sa vishampateh
            Shayaniiyam narendrasya tada asadya vyatishthata || 4
            
            Vanavasam hi sankhyaya vasamsyabharanani cha
            Bhartaram anugacchantyai Sitayai shvashuro dadau || 5
            
            Raja satyam cha dharmam cha raja kulavatam kulam
            Raja mata pita chaiva raja hitakaro nrinam || 6
            
            Niriksha sa muhurtam tu dadarsha Bharato gurum
            Utajeh Rama-masina jataval-kala-dharinam || 7
            
            Yadi buddhih krta drashtum Agastyam tam mahamunim
            Adyaiva gamane buddhim rochayasva mahayasha || 8
            
            Bharatasya aryaputrasya shvashrunam mama cha prabho
            Mrgarupam idam vyaktam vismayam janayishyati || 9
            
            Gaccha shighram ito Rama Sugrivam tam maha-balam
            Vayasyam tam kuru kshipram ito gatva adya Raghava || 10
            
            Desa-kalau prateeksasva ksamamana priyapriye
            Sukha-duhkha sahah kale Sugriva vashago bhava || 11
            
            Vandyaste tu tapah siddhah tapasa vitakalmashah
            Prashtavyashchaapi Sitayah pravrittim vinayanvitaih || 12
            
            Sa nirjitya purim shreshtam Lankaam tam kamarupinim
            Vikramena mahatejah Hanuman Marutatmajah || 13
            
            Dhanya devah sa gandharvah siddhashcha paramarshayah
            Mama pashyanti ye natham Ramam Rajivalochanam || 14
            
            Mangalabhimukhi tasya sa tada asin mahakapeh
            Upatasthe Vishalakshi prayata havyavahanam || 15
            
            Hitam mahar-tham mrudu hetu samhitam
            Vyatitakalayati sampratikshamam
            Nishamya tad vakyam upasthita jvarah
            Prasangavan uttaram etad abravit || 16
            
            Dharmatma rakshasam shresthah samprapto'yam Vibhishana
            Lankaisvaryam dhruvam sriman ayam prapnoty akantakam || 17
            
            Yo vajrapatasani sannipatan
            Na chaksubhe naapi chacala raja
            Sa Ramabanabhihato bhrishar-tah
            Chacala chapam cha mumocha virah || 18
            
            Yasya vikramamasadya rakshasah nidhanam gatah
            Tam manye Raghavam viram narayanamanamayam || 19
            
            Na te dadrisire Ramam dahanta marivahinim
            Mohitah paramastrena Gandharvena mahatmana || 20
            
            Pranamya devatabhyashcha brahmanebhyashcha Maithili
            Baddhanjaliputah chedam uvacha Agni samipatah || 21
            
            Calanat parvatendrasya ganah devascha kampitah
            Chacala Parvati chaapi tada aslishta Maheshvaram || 22
            
            Darah putrah puram rashtram bhogachchhadana bhajanam
            Sarvameva vibhaktam no bhavishyati Harishvara || 23
            
            Yameva ratri Rama pranashalang samavishat
            Tameva ratri Sita api prasuta darakadvayam || 24
            
            Idam Ramayanam krtsnam gayatribhi samyutam
            Trisandhyam yah pathet nityam sarvapapaih pramucyate ||
            `
        }
    ]
    return(
        <div className="mantra">
            <div className="mantraAudio">
                <audio src= '/Songs/shriram.mp3' controls/>
            </div>
            <div className="mantraDisplay">
                <div className="kannada">
                    {gayatriRamayanam.map((mantra)=>{
                        return(
                            <p>{mantra.kannada}</p>
                        )
                    })}
                </div>
                <div className="english">
                    {gayatriRamayanam.map((mantra)=>{
                        return(
                            <p>{mantra.english}</p>
                        )
                    })}
                </div>
            </div>
            
        </div>
    )
}

export default GayatriRamayanam;
