import React from 'react'

function EkaShlokiRamayanam() {
    const ekaShlokiRamayanam = [
        {
            kannada: `     
                  |
               
               ||`

        },
        {
            english: `Adou Rama tapovanadigamana hatvaa mgam kachanam
            Vaidehi haraa Jatayu maraa Sugriva sambhashaam
            Vali nigrahaa samudrataraa Lankapuri dahana
            Paschaad Raavana Kumbhakarna hanana hyetaddhi Rmayaam`
        }
    ]
    return(
        <div className="mantra">
            <div className="mantraAudio">
                <audio src= '/Songs/shriram.mp3' controls/>
            </div>
            <div className="mantraDisplay">
                <div className="kannada">
                    {ekaShlokiRamayanam.map((mantra)=>{
                        return(
                            <p>{mantra.kannada}</p>
                        )
                    })}
                </div>
                <div className="english">
                    {ekaShlokiRamayanam.map((mantra)=>{
                        return(
                            <p>{mantra.english}</p>
                        )
                    })}
                </div>
            </div>
        </div>
    )
}

export default EkaShlokiRamayanam;

